import { Fragment } from "react";
import { PlatformButton } from "components";
import { ButtonType } from "enums";
import { useTranslation } from "react-i18next";
import { useController } from "../../../use-controller";
import { useFormContext } from "react-hook-form";

import Styles from "./index.module.css";

interface Props {
  closeModal: () => void;
}

export const SelectProtonType = ({ closeModal }: Props) => {
  const { t } = useTranslation();
  const { states } = useController();
  const { setValue } = useFormContext();

  let content;
  if (!states.loading) {
    content = (
      <ul className={Styles.list}>
        {!states.loading &&
          states.protonTypes.map(type => (
            <figure onClick={() => setValue("protonType", type)}>
              <img src={type.protonTypeImage} alt="Proton" className={Styles.typeImage} />
            </figure>
          ))}
      </ul>
    );
  }

  return (
    <Fragment>
      <h2 className={Styles.titleSection}>Select a proton</h2>
      <section>{content}</section>
      <PlatformButton
        onClick={() => closeModal()}
        type={ButtonType.BUTTON}
        label={t("common.cancel")}
      />
    </Fragment>
  );
};
