import { Avatar } from "@mui/material";
import { formatOnlyDate } from "utils";
import Astronaut from 'assets/icons/platform/astronaut.svg';
import Amazed from 'assets/icons/reactions/amazed.webp';

import Styles from "./index.module.css";

interface Props {
  timestamp: string;
  description: string;
  toUserImage: string;
  fromUser: string;
  fromUserImage: string;
  protonImage: string;
}

export const ProtonCard = ({ timestamp, description, toUserImage,fromUser, fromUserImage, protonImage }: Props) => {
  return (
    <article className={Styles.protonCard}>
      <p className={Styles.protonCardDate}>{formatOnlyDate(timestamp)}</p>
      <div className={Styles.protonCardBigImages}>
        <Avatar
          className={Styles.bigAvatar}
          src={toUserImage || Astronaut}
        />
        <img src={protonImage || Amazed} alt="proton type"></img>
      </div>
      <p className={Styles.protonCardDescription}>{description}</p>
      <div className={Styles.protonCardFooter}>
        <Avatar
          className={Styles.fromUserImage}
          src={fromUserImage || Astronaut}
          alt={fromUser}
        />
        <p>
          <strong>From:</strong> {fromUser}
        </p>
      </div>
    </article>
  );
};
