import { CircularProgress } from "@mui/material";
import { environment } from "environment";
import { Bill } from "models";
import { useRef, useEffect } from "react";
import { useShallowEqualSelector, RootState } from "store";
import { generateSignature } from "utils";

interface Props {
  bill: Bill;
}

export const SendForm = ({ bill }: Props) => {
  const formRef = useRef<HTMLFormElement>(null);
  const { email } = useShallowEqualSelector((state: RootState) => ({
    email: state.user.email,
  }));

  useEffect(() => {
    if (!formRef.current) {
      return;
    }
    formRef.current.submit();
  }, []);

  const { checkoutUrl, merchantId, accountId, responseUrl, confirmationUrl, apiKey, test } =
    environment.paymentServiceProvider;

  const referenceCode = `manual_payment_joinnexus_${bill.id}_${new Date().toISOString()}`;

  const signature = generateSignature(apiKey, merchantId, referenceCode, bill.price, bill.currency);

  return (
    <form ref={formRef} method="post" action={checkoutUrl}>
      <input name="merchantId" type="hidden" value={merchantId} />
      <input name="accountId" type="hidden" value={accountId} />
      <input name="description" type="hidden" value={`Manual payment of bill ${bill.id}`} />
      <input name="referenceCode" type="hidden" value={referenceCode} />
      <input name="amount" type="hidden" value={bill.price} />
      <input name="tax" type="hidden" value="0" />
      <input name="taxReturnBase" type="hidden" value="0" />
      <input name="currency" type="hidden" value={bill.currency} />
      <input name="signature" type="hidden" value={signature} />
      <input name="test" type="hidden" value={test} />
      <input name="buyerEmail" type="hidden" value={email} />
      <input name="extra1" type="hidden" value={bill.id} />
      {responseUrl && <input name="responseUrl" type="hidden" value={responseUrl} />}
      {confirmationUrl && <input name="confirmationUrl" type="hidden" value={confirmationUrl} />}
      <CircularProgress />
    </form>
  );
};
