import { get, last } from "lodash";
import { LoadMore } from "./load-more";
import { ProtonCard } from "./proton-card";
import { ProtonDisplay } from "./proton-display";
import { RightMenu } from "./right-menu";
import { useController } from "./use-controller";
import { useTranslation } from "react-i18next";
import { GalacticWallpaper, PlatformLayout } from "hoc";
import { PlatformModuleHeader } from "components";

export const ProtonCenter = () => {
  const { states, setStates } = useController();
  const { t } = useTranslation();

  const loadMoreClick = () => {
    const newCursor = get(last(states.protons), "id", 0);
    setStates.setCursor(newCursor);
  };

  return (
    <GalacticWallpaper>
      <PlatformLayout>
        <PlatformModuleHeader label={t("proton_center.proton_center")} />
        <RightMenu
          addProton={states.addProton}
          setAddProton={setStates.setAddProton}
          setSelectedType={setStates.setSelectedType}
        />
        <ProtonDisplay>
          {states.protons.map(singleProton => (
            <ProtonCard
              timestamp={singleProton.createdAt}
              description={singleProton.description}
              toUserImage={singleProton.toUser.picture}
              fromUser={singleProton.fromUser.name}
              fromUserImage={singleProton.fromUser.picture}
              protonImage={singleProton.protonTypeId.protonTypeImage}
              key={singleProton.id}
            />
          ))}
          {!states.protons && <p>{t("proton_center.no_protons")}</p>}
        </ProtonDisplay>
        {states.hasMoreProtons && <LoadMore onClick={loadMoreClick} />}
      </PlatformLayout>
    </GalacticWallpaper>
  );
};
