import GameOptions from "components/game-options/game-options";
import NeonBox from "components/neon-box/neon-box";
import VideoPlayers from "components/video-players/video-players";
import { get } from "lodash";
import { useTranslation } from "react-i18next";
import { RootState, useShallowEqualSelector } from "store";
import { neonBoxStyle } from "styles/neonbox-style";
import { getQuestion, waitingVoteMessage } from "utils";
import { usePendingUsersToVote, useUserIsSupport } from "../../hooks";
import { SkipPlayerButton } from "components";

const ShowQuestion = () => {
  const { t, i18n } = useTranslation();

  const { votes, userId, questionLanguages, gameLanguages } = useShallowEqualSelector(
    (state: RootState) => ({
      votes: state.room.gameState?.votes as Record<number, number>,
      userId: state.user.id,
      questionLanguages: state.room.gameState?.question?.questionLanguages,
      gameLanguages: state.game.game?.gameLanguages,
    }),
  );

  const isSupport = useUserIsSupport();

  const pendingUsersToVote = usePendingUsersToVote();

  const userAnswer = get(votes, Number(userId));

  if (!gameLanguages) {
    return null;
  }

  const question = getQuestion(questionLanguages, i18n);

  let bottomSection = <GameOptions />;

  if (userAnswer || isSupport) {
    bottomSection = <h2 className="neon">{waitingVoteMessage(t, pendingUsersToVote)}</h2>;
  }

  return (
    <>
      <NeonBox style={neonBoxStyle}>
        <h2>{question}</h2>
      </NeonBox>
      <SkipPlayerButton />
      <VideoPlayers />
      <div>{bottomSection}</div>
    </>
  );
};

export default ShowQuestion;
