import { PlatformBox, PlatformButton } from "components";
import React from "react";
import { Plan } from "../interfaces/plan";
import Styles from "./index.module.css";
import rocketImage from "assets/icons/features/rocket.webp";
import { useTranslation } from "react-i18next";

interface Props {
  plans: Array<Plan>;
}

const openWhatsapp = (message: string) => () => {
  const url = `https://api.whatsapp.com/send?phone=573125256655&text=${message}`;
  window?.open(url, "_blank")?.focus();
};

export const Plans: React.FC<Props> = ({ plans }) => {
  const { t } = useTranslation();
  return (
    <div className={Styles.pricingContainer}>
      {plans.map(plan => (
        <PlatformBox className={`${Styles.plan}`} key={plan.name}>
          <div className={Styles.planName}>{t(plan.name)}</div>
          <div>
            <label className={Styles.planPrice}>{plan.price} </label>
            <label>USD</label>
          </div>
          <div className={Styles.planMode}>{t(plan.mode)}</div>
          <ul className={Styles.planFeatures}>
            {plan.features.map((feature, index) => (
              <li key={index}>
                <img className={Styles.rocket} alt="rocket" src={rocketImage} /> {t(feature)}
              </li>
            ))}
          </ul>
          <div>
            <PlatformButton
              label={t("landing.pricing.contact_us")}
              onClick={openWhatsapp(t("landing.whatsapp_message"))}
            />
          </div>
        </PlatformBox>
      ))}
    </div>
  );
};
