import { CircularProgress, TextField } from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers";
import moment from "moment";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import ReactEcharts, { EChartsOption } from "echarts-for-react";
import { PlatformBox } from "components";
import Styles from "./index.module.css";
import { analyticsService } from "services";
import { Feeling } from "enums";

export const FeelingPerMonth = () => {
  const [monthDate, setMonthDate] = useState(moment());
  const { t } = useTranslation();
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const [month, year] = monthDate.format("MM/YYYY").split("/");

  useEffect(() => {
    (async () => {
      setIsLoading(true);
      try {
        const response = await analyticsService.getFeelingsPerMonth(month, year);
        setData(response.data);
      } finally {
        setIsLoading(false);
      }
    })();
  }, [month, year]);

  let content = <CircularProgress />;

  if (!isLoading) {
    if (data.length) {
      const values = new Map(
        data.map(({ _id, totalCount }: { _id: Feeling; totalCount: number }) => [_id, totalCount]),
      );
      const option: EChartsOption = {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        series: [
          {
            name: t("common.feeling"),
            type: "pie",
            radius: "50%",
            data: [
              {
                value: values.get(Feeling.OVERWHELMED) || 0,
                name: t("feelings.overwhelmed"),
                itemStyle: { color: "#F44336" },
              },
              {
                value: values.get(Feeling.SAD) || 0,
                name: t("feelings.sad"),
                itemStyle: { color: "#FF9800" },
              },
              {
                value: values.get(Feeling.NEUTRAL) || 0,
                name: t("feelings.neutral"),
                itemStyle: { color: "#FFEE58" },
              },
              {
                value: values.get(Feeling.FINE) || 0,
                name: t("feelings.fine"),
                itemStyle: { color: "#4CAF50" },
              },
              {
                value: values.get(Feeling.EXCITED) || 0,
                name: t("feelings.excited"),
                itemStyle: { color: "#3F51B5" },
              },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };
      content = <ReactEcharts option={option} theme="dark" />;
    } else {
      content = (
        <div className="center">
          <label className="neon">{t("common.no_data")}</label>
        </div>
      );
    }
  }

  return (
    <>
      <DatePicker
        value={monthDate}
        views={["month", "year"]}
        openTo="month"
        disableFuture={true}
        onChange={value => setMonthDate(value || moment())}
        renderInput={params => {
          return <TextField {...params} label={t("analytics.month_year")} />;
        }}
        minDate={moment("2023-05-05T02:39:04.131Z")}
        inputFormat="MM/YYYY"
      />
      <PlatformBox className={Styles.container}>{content}</PlatformBox>
    </>
  );
};
